//my light theme definition
// If the palette gets no hue value, 500 is used per default. Otherwise the given value.
//values to give: Normal, light, dark
$primary: mat-palette($PrimaryColorLight, 500, 600, 700);
$accent: mat-palette($AccentColorTable, 500, 600, 700);
$warn: mat-palette($WarningColor);

$theme: gds-theme($primary, $accent, $warn, $gds-light-theme-foreground, $gds-light-theme-background, false);
@include angular-material-theme($theme);
@include scrollbar($Elevation01, $Elevation01, $Elevation01);
@include scrollbarShadow($Elevation01, $Elevation01, $Elevation01);
